import React, { useState } from "react"
import Swiper from 'react-id-swiper'
import "../assets/style/small-slider.sass"
import {Link} from 'gatsby'

const SmallSlider = ({title, items, green}) => {
    const [swiper, updateSwiper] = useState({
        slides: 0,
        slideNext: () => null,
        slidePrev: () => null,
        activeIndex: 0,
        isBeginning: true,
        isEnd: false
    });
    const [, update] = useState(false)

    const goNext = () => swiper.slideNext()
    const goPrev = () => swiper.slidePrev()

    const params = {
        slidesPerView: 1,
        effect: 'fade',
        on: {
            slideChange: () => update(up => !up)
        }
    };

    const {slides, isBeginning, isEnd, activeIndex} = swiper;

    const swiperSlides = items.map(({
        node: {
            title, 
            slug,
            acf
        }
    }, index) => (
        <Link to={`offers/${slug}`} className="small-slider__item" key={index}>
            <div className="small-slider__item__title">
                {title}
            </div>
            <div className="small-slider__item__logo">
                {acf.logo ? <img src={acf.logo.source_url} alt="log"/> : null}
            </div>
        </Link>
    ))

    return (<div className={`small-slider ${green && 'green-theme'}`}>
    <h3 className="small-slider__title">
        {title}
    </h3>
    <div className="small-slider__slider">
        <Swiper {...params} getSwiper={updateSwiper}>
            {swiperSlides}
        </Swiper>
        <div className="small-slider__item__actions">
            <div className="small-slider__item__count">
                <span>{activeIndex + 1}</span> / <span>{slides.length}</span>
            </div>
            <div className="small-slider__item__nav">
                <div onClick={goPrev} 
                    className={`small-slider__item__nav-left ${isBeginning && 'disabled'}`}></div>
                <div onClick={goNext} 
                    className={`small-slider__item__nav-right ${isEnd && 'disabled'}`}></div>
            </div>
        </div>
    </div>
</div>
)}
export default SmallSlider
