import React from "react"
import { graphql } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"
import "../assets/style/main.sass"

import Subscribe from "../components/subscribe"
import SmallSilder from "../components/small-slider"
import BigSlider from "../components/big-slider"

const IndexPage = ({ data, pageContext }) => {
  const { edges: discounts } = data.allWordpressWpSale
  const { edges: offers } = data.allWordpressWpOffers
  const { edges: bonus } = data.allWordpressWpBonus
  console.log(discounts, offers, bonus)

  const calltouchOffers = offers.filter(item => item.node.acf.categ.slug === 'calltouch')
  const megafonOffers = offers.filter(item => item.node.acf.categ.slug === 'megafon')

  return (
    <Layout>
      <SEO title="Home" />
      <div className="preview">
        <div className="container">
          <h2 className="preview__title">Магазин умных предложений для вашего бизнеса</h2>
          <div className="preview__subtitle">
            Неважно, кто вы – новичок или уже состоявшийся предприниматель, здесь вы найдете скидки, бонусы и офферы на любой вкус.
          </div>
          <div className="preview__pig"></div>
        </div>
        <div className="preview__money"></div>
      </div>
      <Subscribe />
      <div className="offers-mini">
        <div className="container sbw">
          <SmallSilder title="Предложения от Calltouch" items={calltouchOffers}/>
          <SmallSilder title="Предложения от МегаФон" items={megafonOffers} green/>
        </div>
      </div>
      <BigSlider id="discounts" items={discounts} title="Скидки" />
      <BigSlider id="offers" items={offers} title="Специальные предложения" blue />
      <BigSlider id="bonus" items={bonus} title="Бонусы" />
    </Layout>
  )
}

export const pageQuery = graphql`
  query IndexQuery {
    allWordpressWpSale(
      sort: { fields: date, order: DESC }
    ) {
      edges {
        node {
          title,
          slug,
          acf {
            logo {
              source_url
            }
          }
        }
      }
    }
    allWordpressWpOffers(
      sort: { fields: date, order: DESC }
    ) {
      edges {
        node {
          title,
          slug,
          acf {
            logo {
              source_url
            }
            categ {
              slug
            }
          }
        }
      }
    }
    allWordpressWpBonus(
      sort: { fields: date, order: DESC }
    ) {
      edges {
        node {
          title,
          slug,
          acf {
            logo {
              source_url
            }
          }
        }
      }
    }
  }
`

export default IndexPage
