import React, { useState } from "react"
import { Link } from "gatsby"
import Swiper from 'react-id-swiper'
import "../assets/style/big-slider.sass"

const BigSlider = ({title, items, blue, id}) => {
    const [, update] = useState(false)
    const [swiper, updateSwiper] = useState({
        slides: 0,
        slideNext: () => null,
        slidePrev: () => null,
        isBeginning: true,
        isEnd: false
    });

    const goNext = () => swiper.slideNext()
    const goPrev = () => swiper.slidePrev()

    const params = {
        spaceBetween: 25,
        slidesPerView: 3,
        on: {
            slideChange: () => update(up => !up)
        }
    };

    const {slides, isBeginning, isEnd} = swiper;

    const swiperSlides = items.map(({node: {title, slug, acf}}, index) => (
        <div className="big-slider__item" key={index}>
            <div className="big-slider__item__logo">
                {acf.logo ? <img src={acf.logo.source_url} alt="logo" /> : null}
            </div>
            <div className="big-slider__item__title">
                {title}
            </div>
            <Link to={`${id}/${slug}`} className="big-slider__item__link">
                Узнать подробнее
            </Link>
        </div>
    ))

    return (
    <div id={id} className={`big-slider ${blue && 'blue-theme'}`}>
        <div className="container">
            <h3 className="big-slider__title">
                {title} <span>{slides.length}</span>
            </h3>
            <div className="big-slider__slider">
                <Swiper {...params} getSwiper={updateSwiper}>
                    {swiperSlides}
                </Swiper>
                <div className="big-slider__item__nav">
                    <div onClick={goPrev} className={`big-slider__item__nav-left ${isBeginning && 'disabled'}`}></div>
                    <div onClick={goNext} className={`big-slider__item__nav-right ${isEnd && 'disabled'}`}></div>
                </div>
            </div>
        </div>
    </div>
)}

export default BigSlider
